$side-head-height: 156px;
$side-foot-height: 80px;
.sidebar{
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: $sidebar-width;
  z-index: 999;
  @include transition(all 300ms ease);
  background: #fff;
  box-shadow: 0 2px 24px rgba(#000,0.15);
  @include breakpoint(500) {
    width: 280px;
  }
  .side-header{
    height: $side-head-height;

    .logo{
      text-align: center;
      background-color: $accent-color;
      img{
        width: auto;
        height: 82px;
      }
    }

    .profile{
      width: 100%;
      height: 70px;
      position: relative;
      display: table;
      .picture{
        width: 25%;
        text-align: center;
        vertical-align: middle;
        display: table-cell;
      }
      .designation{
        width: 75%;
        vertical-align: middle;
        display: table-cell;
      }

      .name{
        font-weight: bold;
      }
      .role{
        font-weight: bold;
        font-size: 10px;
      }
      .referral{
        position: absolute;
        top: 0;
        right: 0;
        padding: 2px 6px 2px 12px;
        font-family: monospace;
        font-size: 12px;
        letter-spacing: 1.5px;
        color: #3a3a3a;
        border-bottom-left-radius: 26px;
        background-color: $accent-color;
      }
    }
  }

  .side-body{
    height: calc(100vh - #{$side-head-height} - #{$side-foot-height});
    overflow: auto;
    @include breakpoint(500) {
      height: auto;
    }
  }

  .side-footer{
    height: $side-foot-height;
    padding-top: 20px;
    @include breakpoint(500) {
      padding-top: 0;
    }
    ul{
      li{
        background-color: #fafafa;
        @include breakpoint(500) {
          background-color: transparent;
        }
        &:hover{
          background-color: $danger-color;
          color: #ffffff;
        }
      }
    }
  }

  ul{
    padding: 0;
    margin: 0;
    li{
      display: block;
      line-height: 60px;
      height: 60px;
      font-size: 16px;
      padding: 0 12px;
      border-top: 1px solid rgba(#000,0.08);
      font-weight: bold;
      cursor: pointer;
      color: #000;
      @include transition(all 300ms ease);
      &:hover{
        background-color: $primary-color;
        color: $accent-color;
      }
    }

    a.active{
      li{
        background-color: $primary-color;
        color: #fff;
      }
    }
  }
}
