.my-dashboard{
  width: 100%;
  padding: 18px;
  margin: auto;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 0 24px rgba(#000, 0.25);

  h1{
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 2px;
    margin-bottom: 34px;
    @include breakpoint(500) {
      font-size: 20px;
    }
  }

  .vendor-filters{
    margin-bottom: 72px;

    .ant-select{
      width: 100%;
      height: 52px;
      font-size: 16px;
    }
  }

  .vendor-results{
    .ant-card{
      box-shadow: 0 4px 12px rgba(#000, 0.15);
      border-radius: 12px;
      width: 100%;
      height: 160px;
      position: relative;
      //@include transition(all 300ms ease);
      &:hover{
        top: -2px;
      }
      a{
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 12px;
      }
    }
  }
}
