main {
  width: 100%;
  position: relative;
  min-height: 100vh;
  padding-bottom: 120px;
  .main-bar{
    width: 100%;
    padding: 24px 24px 24px calc(#{$sidebar-width} + 24px) ;
    @include transition(all 300ms ease);
  }

  .sidebar-viewer{
    box-shadow: unset;
    border-radius: 0;
    border-left: unset;
    border-top: unset;
    border-bottom-right-radius: 50%;
  }

  @include screen1200{
    .sidebar{
      @include transform(translate(-#{$sidebar-width}));
    }

    .main-bar{
      padding: 24px;
    }
  }

  &.show-sidebar{
    @include screen1200 {

      .sidebar {
        @include transform(translate(0,0));
      }

      .sidebar-overlay{
        content: '';
        background: rgba(#000,0.65);
        top: 0;
        z-index: 20;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        position: fixed;
        cursor: pointer;
      }
    }
  }

  .sidebar-control{
    display: none;
    @include screen1200{
      z-index: 20;
      display: block;
      position: fixed;
      cursor: pointer;
      left: 0;
      top: 0;
      .triangle{
        fill: $primary-color;
      }

      .anticon{
        position: absolute;
        left: 2px;
        top: 2px;
        font-size: 18px;
        color: #fff;
      }
    }
  }

  .ant-col{
    margin-bottom: 12px;
  }
}

.header{
  margin-bottom: 34px;
  .page-title{
    font-size: 32px;
    font-weight: bold;
  }
}

footer {
  text-align: center;
  position: absolute;
  width: 100%;
  bottom: 0;
  a.ant-btn {
    display: inline-block;
    width: auto;
    padding: 0 4px;
  }
}